.search-form {
    height: 100%;
}

.search-icon {
    border-right: 0;
    background-color: #ededed;
    border-color: white;
    border-top-left-radius: 10em;
    border-bottom-left-radius: 10em;
    transition: all .5s;
}

.search-icon-focused {
    border-right: 0;
    background-color: white;
    border-top-color: #66CC75;
    border-left-color: #66CC75;
    border-bottom-color: #66CC75;
    border-top-left-radius: 10em;
    border-bottom-left-radius: 10em;
    transition: all .5s;
}

.user-search-bar {
    height: 100%;
    font-size: 0.9rem;
    -webkit-appearance: textfield;
    width: 15rem;
    outline: none;
    background-color: #ededed;
    border: solid 1px;
    border-left: 0;
    border-top-color: white;
    border-bottom-color: white;
    border-right-color: white;
    padding: 0 10px 0 0;
    border-top-right-radius: 10em;
    border-bottom-right-radius: 10em;
    transition: all .5s;
}

.user-search-bar::-webkit-search-decoration,
.user-search-bar::-webkit-search-cancel-button {
    display: none;
}

.user-search-bar[type=search]:focus {
    width: 25rem;
    background-color: #fff;
    border-top-color: #66CC75;
    border-right-color: #66CC75;
    border-bottom-color: #66CC75;
    border-left-color: white;
}


.user-search-bar:-moz-placeholder {
    color: #999;
}
.user-search-bar::-webkit-input-placeholder {
    color: #999;
}

.navbar-nav li {
    padding-right: .7rem;
}

.log-in-button {
    border-color: #66CC75;
    color: #66CC75;
    font-size: 0.9rem;
    padding-left: 1.3rem;
    padding-right: 1.3rem;
}

.log-in-button:hover {
    background-color: white;
    border-color: #66CC75;
    color: #66CC75;
    font-size: 0.9rem;
    padding-left: 1.3rem;
    padding-right: 1.3rem;
}

